
.homeContainer {
    background: linear-gradient(180deg, rgb(125, 139, 228) 0%, rgb(94, 47, 156) 100%);
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: -2;
    overflow: hidden;
}
.homepageItems {
    width: 100%;
    height: 100%;
    position: absolute;
}
.pageLink:hover {
    color: rgb(94, 47, 156);
}
.animationText {
    position: absolute;
    z-index: 4;
    bottom: 2%;
    text-align: center;
    width: 100%;
    color: white;
    visibility: visible;
}

.linkContainer {
    flex-direction: row;
    flex-wrap: wrap;
    text-align: left;
    margin: 5% 12% 10% 12%;
    justify-content: space-between;
    align-items: center;
    display: flex;
    z-index: 1;
    position: relative;
}
.pageLink {
    color:black;
    z-index: 2;
    position: relative;
    font-weight: bold;
}
.bitmoji{
    display: none;
}
.whiteBox {
    position: absolute;
    top: 0;
    width: 90%;
    height: 80%;
    margin: 5%;
    background-color: white;
    z-index: -1;
    
}
@keyframes falldown {
    from {top: 0vh;}
    to {top: 100vh;}
}
.text {
    white-space: pre-line;
    text-align: left;
    font-weight: bolder;
    font-size: larger;
    display:inline-flexbox;
    margin: 20% 10% 10% 10%;
    width: 65%;
    position: relative;
    font-size: 30px;
    transition: all 0.5s ease-in;
}

.mobileOnly {
    visibility: hidden;
}

@media screen and (min-width: 1050px) {
    .bitmoji{
        display: block;
        position: absolute;
        top: 20%;
        left: 65%;
    }

    .pageLink {
        font-size: 30px;
    
    }
    .linkContainer{
        width: 45%;
    }
    /* .falldownContainer {
        animation-name: falldown;
        animation-duration: 10s;
        position: absolute;
        z-index: 1;
        margin-left: 20%;
    } */
    .whiteBox {
        border-radius: 60px;
    }
    .text {
        position: relative;
        margin: 10% 0 5% 12%;
        width: 40%;
        font-size: 36px;
    }
    
}
@media screen and (max-width: 768px) {
   
    .linkContainer {
        flex-direction: row;
        text-align: center;
        margin-top: 0;
        margin-bottom: 0;
        margin-left: auto;
        margin-right: auto;
        justify-content: space-evenly;
        align-items: center;
        z-index: 1;
        flex-wrap: wrap;
        
        position: relative;
    }
    .pageLink {
        font-size: 18px;
        margin-bottom: 10%;
    }
    
    .text {
        font-size: 26px;
        margin-left: auto;
        margin-right: auto;
        margin-top: 35%;
        text-align: center;
    }
    .animationText {
        visibility: hidden;
    }
    .whiteBox {
        height: 65%;
        margin-top: 17.5%;
        margin-bottom: 17.5%;
    }
    .linkIcon {
        color: #fff;
        font-size: 30px;
        margin-left: 1%;
        margin-right: 1%;
    }
}

@media screen and (max-width: 450px) {
    .pcOnly {
        display: none;
    }
   
    .linkContainer {
        flex-direction: row;
        text-align: center;
        margin-top: 0;
        margin-bottom: 0;
        margin-left: auto;
        margin-right: auto;
        justify-content: space-evenly;
        align-items: center;
        z-index: 1;
        flex-wrap: wrap;
        
        position: relative;
    }
    .pageLink {
        font-size: 18px;
        margin-bottom: 10%;
    }
    .text {
        font-size: 22px;
        margin-left: auto;
        margin-right: auto;
        margin-top: 35%;
        text-align: center;
    }
    .animationText {
        visibility: hidden;
    }
    .whiteBox {
        height: 65%;
        margin-top: 17.5%;
        margin-bottom: 17.5%;
    }
    .mobileOnly {
        visibility: visible;
    }
    .linkIcon {
        color: #fff;
        font-size: 30px;
        margin-left: 1%;
        margin-right: 1%;
        text-decoration: none;
    }
    .linkIcon:hover {
        color: #fff;
        text-decoration: none;
    }
    .iconContainer {
        position: absolute;
        bottom: 15%;
        width: 100%;
        align-items: center;
    }
}